import { z } from 'zod';

export const PreAggregateSchema = z.object({
  type: z.enum(['rollup', 'original_sql', 'rollup_join']).optional(),
  measures: z.array(z.string()).optional(),
  dimensions: z.array(z.string()).optional(),
  segments: z.array(z.string()).optional(),
  timeDimension: z.string().optional(),
  granularity: z.string().optional(),
  partitionGranularity: z.string().optional(),
  rollups: z.array(z.string()).optional(),
  refreshKeySql: z.string().optional(),
  refreshKeyEvery: z.string().optional(),
  refreshKeyIncremental: z.boolean().optional(),
  refreshKeyUpdateWindow: z.string().optional(),
  buildRangeStartSql: z.string().optional(),
  buildRangeEndSql: z.string().optional(),
  lambda: z.boolean().optional(),
});

export interface PreAggregateInterface {
  type?: 'rollup' | 'original_sql' | 'rollup_join';
  measures?: Array<string>;
  dimensions?: Array<string>;
  segments?: Array<string>;
  drillDown?: Array<string>;
  timeDimension?: string;
  granularity?: string;
  partitionGranularity?: string;
  rollups?: Array<string>;
  refreshKeySql?: string;
  refreshKeyEvery?: string;
  refreshKeyIncremental?: boolean;
  refreshKeyUpdateWindow?: string;
  buildRangeStartSql?: string;
  buildRangeEndSql?: string;
  lambda?: boolean;
  external?: boolean;
}
